.login-app {
  background-color: #131835;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-app form {
  height: 520px;
  width: 90%;
  max-width: 400px;
  background-color: rgba(255, 255, 255, 0.13);

  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
  padding: 50px 35px;
}
.login-app form * {
  font-family: "Poppins", sans-serif;
  color: #ffffff;
  letter-spacing: 0.5px;
  outline: none;
  border: none;
}
.login-app form h3 {
  font-size: 32px;
  font-weight: 500;
  line-height: 42px;
  text-align: center;
}

.login-app .error-msg {
  color: #f44336;
  margin-top: 12px;
  font-size: 14px;
}

.login-app label {
  display: block;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 500;
}
.login-app input {
  display: block;
  height: 50px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 3px;
  padding: 0 10px;
  margin-top: 8px;
  font-size: 14px;
  font-weight: 300;
}

.login-app :is(button, input):focus {
  outline: solid #00bcd4 4px;
}

.login-app ::placeholder {
  color: #b8b8b8;
}
.login-app button {
  margin-top: 50px;
  width: 100%;
  background-color: #ffffff;
  color: #080710;
  padding: 15px 0;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;
}
